<template>
<section>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('InTransitItems') }}
                        </h5>
                        <button v-on:click="CloseAddAcceptRejectPopup()" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>

                    </div>
                    <div class="modal-body">
                        <div class="listing">
                            <div class="table-responsive tableborder" style="overflow-x:auto;">
                                <table class="table-sorting table table-bordered" id="table-openactivities-content" style="width:1500px !important;">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th style="width:50px !important;" class="text-center" id="nonresize">
                                                <input id="chkedAll" v-model="checkAll" class="chkItems" type="checkbox" @click="CheckMultipleCheckbox($event)">
                                            </th>
                                            <th><span>{{ $t('ItemName') }}</span></th>
                                            <th><span>{{$t('Unique Name')}}</span></th>
                                            <th><span>{{$t('FromLocation')}}</span></th>
                                            <th><span>{{$t('ItemCode')}}</span></th>
                                            <th><span> {{$t('ShippedQuantity')}}</span></th>
                                            <th><span> {{$t('ReceivedQuantity')}}</span></th>
                                            <th><span>{{$t('AcceptRejectTicket')}}</span></th>
                                            <th><span>{{$t('Comments')}}</span></th>
                                            <th><span>{{$t('Action')}}</span></th>		

                                        </tr>
                                    </thead>
                                    <tbody>

                                        <tr v-for="(item, index) in InTransitData " :key="item.assetCatalougeId" :index=index>
                                            <td class="text-center">
                                                <input class="chkItems" type="checkbox"   v-model="item.isSelected" @click="CheckSingleCheckbox($event,item)" />
                                            <th style="width:50px !important;" class="text-center" id="nonresize"> </th>
                                            <!-- <input class="chkItems" :value="item.ASSET_CATALOGUE_ID" type="checkbox"v-model="TransitRecords></th> -->
                                            </td>
                                            <td>{{ item.name }}</td>
                                            <td>{{ item.uniqueName }}</td>
                                            <td>{{ item.locationName }}</td>
                                            <td>{{ item.itemCode }}</td>
                                            <td v-if="item.CreateUniqueNameListing" class="text-center single-action">
                                                <span class="text-dark" title="@item.Quantity">
                                                    <i class="fa fa-list" aria-hidden="true"></i><span class="noti-circle noti-blue">
                                                        {{ item.quantityShipped }}
                                                    </span>
                                                </span>
                                            </td>

                                            <td v-if="!item.CreateUniqueNameListing"  class="text-center single-action">
                                                    <span class="text-dark" title="@item.Quantity">
                                                        <i class="fa fa-list" aria-hidden="true"></i><span class="noti-circle noti-blue">
                                                            {{ item.quantityShipped }}
                                                        </span>
                                                    </span>
                                                </td>

                                            <td>
                                                <input v-if="Boolean(item.quantity)" type="text" class="form-control" v-model="item.quantity" />
                                                <input v-else type="text" class="form-control" v-model="item.receviedQuantity" />
                                            </td>
                                            <td>
                                                <label class="switch medium">
                                                    <input type="checkbox" :value="1"  :checked="item.isAcceptReject" v-model="item.isAcceptReject"  class="form-check-input rdoticketfor custom-control-input dynamic custom-switch inTransit_Accept">
                                                    <span class="slider round">
                                                        <span class="slider-yes">Accept</span>
                                                        <span class="slider-no">Reject</span>
                                                    </span>
                                                </label>
                                            </td>
                                            <td>
                                                <textarea name="comments" id="txtComment_0" onkeypress="return" maxlength="2000" rows="3" cols="40" v-model="item.acceptRejectComment"></textarea>
                                            </td>

                                            <td class="text-center">
                                                <a href="javascript:;" @click="RaiseDisputePopUp(item)" data-toggle-tooltip="tooltip" data-original-title="Raise Dispute"><i class="fa fa-hand-pointer-o fa-2x "></i></a>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>

                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                        <div class="row flex-row-reverse">
                            <div class="col-lg-4 text-right">

                                <span>
                                    <a  v-if="isAcceptAll" @click="ChangeStatus($event)" class="btn btn-info formbtn text-white m-1"   data-toggle-tooltip="tooltip" data-original-title="AcceptAll"><i class="fa fa-check pr-2"></i>AcceptAll</a>

                                </span>
                                <span>
                                    <a v-if="!isAcceptAll" @click="ChangeStatus($event)" class="btn btn-danger formbtn text-white m-1"   title=""   data-toggle-tooltip="tooltip" data-original-title="Reject All"><i class="fa fa-close pr-2"></i>
                                        RejectAll</a>
                                </span>

                                <a href="javascript:;" v-on:click="SubmitData()" class="btn btn-success formbtn text-white m-1" :title="$t('Save')"><i class="fa fa-save pr-2"></i>
                                    <span>{{ $t('Save') }}</span></a>
                                <a href="javascript:;" v-on:click="onCancel()" :title="$t('Cancel')" class="btn btn-danger formbtn text-white m-2"><i class="fa fa-close pr-2"></i><span>{{
                                            $t('Cancel') }}</span></a>
                            </div>

                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
    <RaiseDisputePopUp v-if="Raisepopup" @CloseRaiseDisputePopUp="CloseRaiseDisputePopUp" :Raisedata="Raisedata">
    </RaiseDisputePopUp>
</section>
</template>

<script>
import DataService from '../../service/DataService'

import RaiseDisputePopUp from '../Modal/RaiseDisputePopup'
export default {
    components: {
        RaiseDisputePopUp
    },
    props: {
        DetalisData: {
            type: Array,
            required: true
        },

    },
    data() {
        return {
            InTransitData: [],
            TransitRecords: '',
            IsShowPopup: false,
            assetCatelogueId: '',
            locationId: '',
            assetTransferDetailsId: '',
            assetTransferBatchId: '',
            IsAcceptReject: 1,
            isSlider: false,
            data: [],
            checkedArray: [],
            Raisepopup: false,
            Raisedata: [],
            AcceeptBtn: false,
            RejectBtn: true,
            checkAll:false,
            isAcceptAll:true
        }
    },
   async created() {
       
        var ths = this;
       await  ths.FetchData();
    },
    methods: {
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];

        },
        CloseAddAcceptRejectPopup: function () {
            this.$emit('CloseAddAcceptRejectPopup')
        },
        onCancel: function () {
          
            this.CloseAddAcceptRejectPopup();
        },

        FetchData:async function () {
          
            var vm = this;
            vm.data=[];
            vm.DetalisData.forEach(element => {
                var obj = {
                    AssetCatalogueId: element.assetCatalogueId,
                    LocationId: element.locationto,
                    AssetTransferDetailId: element.asset_transfer_deatails_Id,
                    AssetTransferBatchId: element.btach_Id,
                    CreatedAt:  element.createdAt,
                }
                vm.data.push(obj);
            })

             await  DataService.SaveMultipleInTransitItems(vm.data).then(response => {
               
                vm.InTransitData=[];
                if (response.data != null) {
                    if (response.data.result.length > 0) {
                     
                      
                        response.data.result.forEach(element => {
                               var obj=element;
                               obj.isSelected=false;
                               obj.quantityShipped=element.quantity;
                                vm.InTransitData.push(obj);
                            })
                     
                        vm.noRecord = false;
                    } else {
                        vm.noRecord = true;

                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        },

        ChangeStatus(ev) {
        
            var vm = this;

            if (vm.isAcceptAll) {
                vm.InTransitData.forEach(element => {
                   element.isAcceptReject=true;
                })
                vm.isAcceptAll=false;

            } else {
                vm.InTransitData.forEach(element => {
                   element.isAcceptReject=false;
                })
                vm.isAcceptAll=true;
            }

        },

     async SubmitData() {
       
            var vm = this;
            var ids = "";

            vm.InTransitData.forEach(ele => {

                if(ele.isSelected){

                vm.atid = ele.asseT_TRANSFER_DETAILS_ID;
                vm.rid = ele.relateD_INFO_ID;
                vm.qty = ele.quantity;
                vm.rdo = ele.isAcceptReject==true?1:0;
                vm.rqty = ele.receviedQuantity;
                vm.rcom = ele.acceptRejectComment==undefined?'':ele.acceptRejectComment;
                ids += vm.atid + '*' + vm.rid + '*' + vm.rqty + '*' + vm.rdo + '*' + vm.qty + '*' + vm.rcom + '|';
              
                }          
            });
            if (ids == "") {
                vm.ShowAlert(vm.$t("Please select at least one record."), "warning", vm.$t('Alert'));
                return;
            }
            var Objdata = `MoveToInTransitInfo=${ids}&assetTransferBatchId=${vm.DetalisData[0].btach_Id}`
            await  DataService.UpdateInTransitItems(Objdata).then(response => {

             
                if (response.data.result=='success') {
                    vm.ShowAlert(response.data.msg, "success", vm.$t('Alert'));
                  vm.$parent.FetchData();
                    vm.CloseAddAcceptRejectPopup();
                    vm.isLoading = false;


                } else {
                    vm.isLoading = false;
                    vm.ShowAlert(response.data.msg, "faliure", vm.$t('Alert'));
                }

            })

        },

        CheckSingleCheckbox(event,item) {
           
            var vm = this;
            vm.checkAll=true;

            if (event.currentTarget.checked) {
                item.isSelected=true;
                vm.InTransitData.forEach(element => {
                    if (!element.isSelected) {
                        vm.checkAll=false;
                    }
                })
            } else {
                item.isSelected=false;
                vm.checkAll=false;
            }

        },

        RaiseDisputePopUp(ev) {
            
            var vm = this;
            vm.Raisedata = ev;
            vm.Raisepopup = true;

        },

        CloseRaiseDisputePopUp: function () {
            var vm = this;
            vm.Raisepopup = false;
        },
        onCancel: function () {
            
            this.$emit('CloseAddAcceptRejectPopup')
        },
        CheckMultipleCheckbox(ev) {
  
            var vm = this;
       
            if (ev.currentTarget.checked) {
                vm.InTransitData.forEach(element => {
                               element.isSelected=true;
            })
            } else {
                vm.InTransitData.forEach(element => {
                               element.isSelected=false;
            })
            }
           
        }

    }
}
</script>
