<template>
    <section>
        <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset"
            IdentityColumn="btach_Id" HeaderText="In-Transit List" :ListData="IntransitListData"
            :HeaderData="Headers" :widgets="widgets" :showCheckBox="true" :callbackfunction="FetchData"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray"
            :SortExp="SortExp" :SortBy="SortBy" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata"  :NorecordfoundText="$t('NoRecordfound')">

           <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'shippedquantity'">
                <td class="text-center single-action">
                <span class="text-dark" :title="data.row.NUMBER_OF_ITEM">
                    <i class="fa fa-list" aria-hidden="true"></i>
                    <span class="noti-circle noti-blue">{{ data.row[data.column.COLUMN_NAME] }}</span>
                </span>
              </td>
            </template>
           <template v-if="data.column.COLUMN_NAME == 'status'">
            <td class="text-center single-action"  v-if="data.row.status=='In-Transit'">
                <span class="active-disabled">
                    <span class="btn d-inline-block text-center text-truncate btn-info">{{ data.row[data.column.COLUMN_NAME] }}</span>
                </span>
            </td>
            <td class="active-disabled" v-if="data.row.status=='Dispute Raised'">
                <span class="active-disabled"  >
                    <span class="btn d-inline-block text-center text-truncate legend-pending"> {{data.row[data.column.COLUMN_NAME]}}</span>
                </span>
            </td>
           </template>
           <template v-if="data.column.COLUMN_NAME=='description'">
            <td class="text-center single-action">
            <span class="text-dark" :title="data.row.NUMBER_OF_ITEM">
                <a href="javascript:;"    data-original-title="Comments"  @click="OpenCommentpopup(data.row)">
                    <em class="fa fa-eye text-info"></em>
                </a>
            </span>
        </td>
        </template>


           </template>
        </tg-list>
<IProductAcceptReject v-if="IsShowPopup" @CloseAddAcceptRejectPopup="CloseAddAcceptRejectPopup"  :DetalisData="DetalisData"></IProductAcceptReject>
<DescriptionPopUp v-if="Commentpopup" @ClosePopup="ClosePopup" :assetTransferdetailId="assetTransferdetailId" :description="description"
:assetTransferBatchId="assetTransferBatchId" :itemName="itemName" :relatedInfoId="relatedInfoId"></DescriptionPopUp>
    </section>
</template>

<script>

import DataService from '../../service/DataService'
import Schema from '../AssetCatalog/ViewInTransitItemsSchema'
import IProductAcceptReject from '../../components/Modal/InTransistProductAcceptReject'
import DescriptionPopUp from "../../components/Modal/DescriptionPopUp.vue"

export default {
    components: {
        IProductAcceptReject,DescriptionPopUp
},
    data() {
        return {
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            IntransitListData: [],
            pageNum: 1,
            search: "",
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            PageSizeOption: null,
            StatusListForListing: [],
            DetalisData:[],
            Headers: [
            {
                "COLUMN_NAME": "description",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Description",
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "sourceLocation",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Location From",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "destinationLocation",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Location To",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "shippedquantity",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "ShippedQuantity",
                "DISPLAY_ORDER": 4,
                settings : {
                            isInSlot : true
                        }

            },
            {
                "COLUMN_NAME": "status",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5,
                settings : {
                            isInSlot : true
                        }

            },
            {
                "COLUMN_NAME": "shippedby",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Shipped By",
                "DISPLAY_ORDER": 6,

            },
            {
                "COLUMN_NAME": "description",
               "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Comments",
                "DISPLAY_ORDER": 7,
                settings:{
                    isInSlot:true
                }

            },

            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            sortBy: "",
            sortExp: "",
        
            IsShowPopup:false,

              PageSize:10,
              pageNum:1,
              locationId:'',
              locationfrom:'',
              searchcondition:'',
              datefromate:'',
              disputeval:'',
              statusId:'',
              Commentpopup:false,
             assetTransferdetailId:'',
             assetTransferBatchId:'',
             itemName:'',
             OperationCode:'',
             relatedInfoId:'',
             description:""
          
        }
    },
   async created() {
        var ths = this;

        Schema.bindleftCommonSearchdropdown(ths, "Asset", "Location");
        Schema.bindleftCommonSearchdropdown(ths, "Asset", "Locationto");  
       await ths.FetchData();

        ths.LegendArray = [
            {
                class: 'legend-pending colorboxsquare',
                text: "Disputed Products"
            }
        ]

    },
    methods: {
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];
        },
       async  FetchData() {
            var vm=this;           
           var Params = `StatusIds=${vm.statusId}&dateFormat=${vm.datefromate}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.sortBy}&sortExp=${vm.sortExp}&${vm.searchCondition}`;
          await  DataService.GetTransitItemsList(Params).then(response => {                
                if (response.data != null) {
                    console.log('resp',response.data)
                    if (response.data.length > 0) {                   

                        vm.IntransitListData = response.data;
                        vm.IntransitListData.forEach((row)=>{
                            row.isCheckBoxDisabled = false;
                            row.additionalClass = '';
                            if(row.CreateUniqueNameListing){
                                row.additionalClass=='low-bar odd';
                            }
                            if(row.status_code == "DISPUTE_RAISED"){
                                row.additionalClass ='warning-bar';
                            }                    
                            if (row.status == 'Dispute Raised') {
                            row.isCheckBoxDisabled = true;
                            }
                        });

                        // for (var row in vm.IntransitListData) {
                        //   vm.IntransitListData[row].isCheckBoxDisabled = false;
                        //   if (vm.IntransitListData[row].status == 'Dispute Raised') {
                        //     vm.IntransitListData[row].isCheckBoxDisabled = true;
                            
                        //   }
                        // }
                        vm.TotalRecords = response.data[0].total;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.IntransitListData = [];
                        vm.TotalRecords = 0
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        },
        leftCommonSearch: function (value) { 
                    
        this.PageNumber = value.PageNumber;
        this.isPaged = 1;
        if (typeof value.searchCondition != 'undefined') {
            this.searchCondition = value.searchConditionJson
;
        }
        else {
            this.searchCondition = "";
        }
        this.FetchData();
       },


        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        commonHeaderButtonClick(event) {            
            switch (event.callbackfunction) {
                case "acceptrejectpopup":
                    this.AddAcceptRejectPopup();
                    break;
                case "TransferredList":
                    this.OpenTransferredList();
                    break;
                case "BacktoList":
                    this.OpenBacktoList();
                    break;
            }
        },

        ClosePopup:function(){
            var vm = this;
            vm.Commentpopup=false;
        },

        OpenTransferredList() {
            var vm = this;
            vm.$router.push({ path: 'ViewTransferedItem' });
        },

        OpenBacktoList() {
            var vm = this;
          
            vm.$router.push({ path: 'Index' });
        },
        
        AddAcceptRejectPopup:function() { 
         
            var vm=this;
            var val = "";
            var temp=[];
          $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
         
            for (let i= 0; i<vm.IntransitListData.length; i++) {
            if (vm.IntransitListData[i].btach_Id  == $(this).val() ) {
              
                temp.push(vm.IntransitListData[i]);                
                console.log('new',vm.DetalisData)
            }
        }
            if (val.length > 0)
                val += ',';
            val += $(this).val();
        });
        

       temp= temp.reduce((acc, current) => {
            const x = acc.find(item => item.batchId === current.batchId);
            if (!x) {
                return acc.concat([current]);
            } else {
                return acc;
            }
         }, []);

        if (val == "") {
         vm.ShowAlert(
          vm.$t("Please select at least one record."),
          "warning",
          vm.$t("Alert")
       );
        return false;
       } 
        vm.DetalisData=temp;
        vm.IsShowPopup=true; 
     
    },



       OpenCommentpopup(ths){
         
            var vm = this;
            vm.relatedInfoId=ths.relateD_INFO_ID,
            vm.assetTransferdetailId=ths.asset_transfer_deatails_Id;
            vm.assetTransferBatchId=ths.btach_Id;
            vm.description=ths.description;
            vm.itemName=ths.name;
            vm.Commentpopup=true
        },
        
    
        CloseAddAcceptRejectPopup()
        {
            var vm=this;
            vm.IsShowPopup=false;
        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortBy == "") {
                    this.sortBy = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
    },

}

</script>